'use client';

import { useState } from 'react';
import { createPortal } from 'react-dom';
import { X, Mail, Lock, User, ArrowRight } from 'lucide-react';
import { useAuth } from '../../contexts/AuthContext';
import { careersApi } from '../../lib/api/careersApi';

interface CandidateAuthModalProps {
    isOpen: boolean;
    onClose: () => void;
    onSuccess?: () => void;
}

export default function CandidateAuthModal({ isOpen, onClose, onSuccess }: CandidateAuthModalProps) {
    const [mode, setMode] = useState<'login' | 'register'>('login');
    const { loginCandidate } = useAuth();
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    // Form State
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [firstName, setFirstName] = useState('');
    const [lastName, setLastName] = useState('');

    if (!isOpen) return null;

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError(null);

        try {
            let response;
            if (mode === 'register') {
                response = await careersApi.register({
                    email,
                    password,
                    firstName,
                    lastName,
                });
            } else {
                response = await careersApi.login({ email, password });
            }

            loginCandidate(response.access_token, response.user);
            if (onSuccess) onSuccess();
            onClose();
        } catch (err: any) {
            setError(err.message || 'Une erreur est survenue');
        } finally {
            setIsLoading(false);
        }
    };

    return createPortal(
        <div style={{
            position: 'fixed', inset: 0, zIndex: 9999,
            background: 'rgba(0,0,0,0.5)', backdropFilter: 'blur(4px)',
            display: 'flex', alignItems: 'center', justifyContent: 'center'
        }} onClick={onClose}>
            <div style={{
                background: 'white', width: '100%', maxWidth: '420px',
                borderRadius: '24px', padding: '32px', position: 'relative',
                boxShadow: '0 20px 25px -5px rgba(0, 0, 0, 0.1)'
            }} onClick={e => e.stopPropagation()}>

                {/* Close Button */}
                <button onClick={onClose} style={{
                    position: 'absolute', top: '24px', right: '24px',
                    background: 'none', border: 'none', cursor: 'pointer', color: '#9CA3AF'
                }}>
                    <X size={24} />
                </button>

                {/* Header */}
                <div style={{ textAlign: 'center', marginBottom: '32px' }}>
                    <div style={{
                        width: '64px', height: '64px', background: 'var(--color-primary-50)',
                        borderRadius: '50%', margin: '0 auto 16px', display: 'flex',
                        alignItems: 'center', justifyContent: 'center', color: 'var(--color-primary)'
                    }}>
                        <Lock size={32} />
                    </div>
                    <h2 style={{ fontSize: '24px', fontWeight: 'bold', color: '#111827', marginBottom: '8px' }}>
                        {mode === 'login' ? 'Espace Candidat' : 'Cr√©er un compte'}
                    </h2>
                    <p style={{ color: '#6B7280', fontSize: '15px' }}>
                        {mode === 'login'
                            ? 'Connectez-vous pour suivre vos candidatures'
                            : 'Rejoignez le vivier de talents KomoraLink'}
                    </p>
                </div>

                {/* Form */}
                <form onSubmit={handleSubmit} style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>

                    {/* Error Message */}
                    {error && (
                        <div style={{
                            padding: '12px 16px',
                            background: '#FEE2E2',
                            borderRadius: '8px',
                            color: '#DC2626',
                            fontSize: '14px'
                        }}>
                            {error}
                        </div>
                    )}

                    {mode === 'register' && (
                        <div style={{ display: 'flex', gap: '12px' }}>
                            <input
                                type="text" placeholder="Pr√©nom" required
                                value={firstName} onChange={e => setFirstName(e.target.value)}
                                style={inputStyle}
                            />
                            <input
                                type="text" placeholder="Nom" required
                                value={lastName} onChange={e => setLastName(e.target.value)}
                                style={inputStyle}
                            />
                        </div>
                    )}

                    <div style={{ position: 'relative' }}>
                        <Mail size={18} color="#9CA3AF" style={{ position: 'absolute', left: '16px', top: '50%', transform: 'translateY(-50%)' }} />
                        <input
                            type="email" placeholder="Adresse email" required
                            value={email} onChange={e => setEmail(e.target.value)}
                            style={{ ...inputStyle, paddingLeft: '44px' }}
                        />
                    </div>

                    <div style={{ position: 'relative' }}>
                        <Lock size={18} color="#9CA3AF" style={{ position: 'absolute', left: '16px', top: '50%', transform: 'translateY(-50%)' }} />
                        <input
                            type="password" placeholder="Mot de passe" required
                            value={password} onChange={e => setPassword(e.target.value)}
                            style={{ ...inputStyle, paddingLeft: '44px' }}
                        />
                    </div>

                    <button type="submit" disabled={isLoading} style={{
                        marginTop: '8px', padding: '14px', borderRadius: '12px',
                        background: 'var(--color-primary)', color: 'white', border: 'none',
                        fontWeight: '600', fontSize: '16px', cursor: isLoading ? 'wait' : 'pointer',
                        display: 'flex', alignItems: 'center', justifyContent: 'center', gap: '8px',
                        opacity: isLoading ? 0.7 : 1
                    }}>
                        {isLoading ? 'Chargement...' : (mode === 'login' ? 'Se connecter' : 'S\'inscrire')}
                        {!isLoading && <ArrowRight size={18} />}
                    </button>
                </form>

                {/* Footer Switcher */}
                <div style={{ marginTop: '24px', textAlign: 'center', fontSize: '14px', color: '#6B7280' }}>
                    {mode === 'login' ? "Pas encore de compte ? " : "D√©j√† un compte ? "}
                    <button
                        onClick={() => setMode(mode === 'login' ? 'register' : 'login')}
                        style={{ background: 'none', border: 'none', color: 'var(--color-primary)', fontWeight: '600', cursor: 'pointer', padding: 0 }}
                    >
                        {mode === 'login' ? "Cr√©er un compte" : "Se connecter"}
                    </button>
                </div>

            </div>
        </div>,
        document.body
    );
}

const inputStyle = {
    width: '100%',
    padding: '12px 16px',
    borderRadius: '12px',
    border: '1px solid #E5E7EB',
    background: '#F9FAFB',
    fontSize: '15px',
    outline: 'none',
    transition: 'border-color 0.2s',
};
