const CAREERS_API_URL = process.env.NEXT_PUBLIC_CAREERS_API_URL || 'http://localhost:3002/api';

interface AuthResponse {
    access_token: string;
    user: {
        id: string;
        email: string;
        firstName: string;
        lastName: string;
    };
}

interface RegisterPayload {
    email: string;
    password: string;
    firstName: string;
    lastName: string;
    phone?: string;
}

interface LoginPayload {
    email: string;
    password: string;
}

interface Job {
    id: string;
    title: string;
    department: string;
    location: string;
    type: string;
    description: string;
    requirements: string;
    isActive: boolean;
    createdAt: string;
}

interface ApplicationPayload {
    jobId: string;
    coverLetter?: string;
}

export const careersApi = {
    // Auth
    async register(payload: RegisterPayload): Promise<AuthResponse> {
        const res = await fetch(`${CAREERS_API_URL}/auth/register`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(payload),
        });

        if (!res.ok) {
            const error = await res.json();
            throw new Error(error.message || 'Registration failed');
        }

        return res.json();
    },

    async login(payload: LoginPayload): Promise<AuthResponse> {
        const res = await fetch(`${CAREERS_API_URL}/auth/login`, {
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify(payload),
        });

        if (!res.ok) {
            const error = await res.json();
            throw new Error(error.message || 'Login failed');
        }

        return res.json();
    },

    // Jobs
    async getJobs(): Promise<Job[]> {
        const res = await fetch(`${CAREERS_API_URL}/jobs`);
        if (!res.ok) throw new Error('Failed to fetch jobs');
        return res.json();
    },

    async getJob(id: string): Promise<Job> {
        const res = await fetch(`${CAREERS_API_URL}/jobs/${id}`);
        if (!res.ok) throw new Error('Job not found');
        return res.json();
    },

    // Applications
    async submitApplication(payload: ApplicationPayload, token: string): Promise<any> {
        const res = await fetch(`${CAREERS_API_URL}/applications`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${token}`,
            },
            body: JSON.stringify(payload),
        });

        if (!res.ok) {
            const error = await res.json();
            throw new Error(error.message || 'Application submission failed');
        }

        return res.json();
    },
};

export type { AuthResponse, RegisterPayload, LoginPayload, Job, ApplicationPayload };
